.top-navigator {
  background-color: darkgray;
  border-radius: 4px;
  bottom: 24px;
  cursor: pointer;
  font-size: 24px;
  height: 0;
  -ms-flex-pack: center;
      justify-content: center;
  line-height: 44px;
  opacity: 0;
  position: fixed;
  right: 24px;
  text-align: center;
  -webkit-transition: opacity 1s ease;
  -o-transition: opacity 1s ease;
  transition: opacity 1s ease;
  width: 36px;
}

.top-navigator.show {
  height: 36px;
  opacity: 1;
}



