.logo-links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  left: -36px;
  opacity: 0;
  position: fixed;
  -webkit-transition: left 1.5s ease, opacity 0.5s ease;
  -o-transition: left 1.5s ease, opacity 0.5s ease;
  transition: left 1.5s ease, opacity 0.5s ease;
}

.logo-links.show {
  left: 8px;
  opacity: 1;
}



