.reliability-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.quality {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-preferred-size: calc(100% - 72px);
      flex-basis: calc(100% - 72px);
  margin: 24px;
}

.reliability-title {
  margin-left: 24px;
  text-align: left;
  width: 340px;
}

.ship-icon,
.user-focused-icon,
.responsive-icon,
.code-quality-icon {
  background-repeat: no-repeat;
  background-size: contain;
  height: 48px;
  opacity: 0.8;
  width: 48px;
}

.ship-icon {
  background-image: url('../images/ship.png');
}

.user-focused-icon {
  background-image: url('../images/user-focused.png');
}

.responsive-icon {
  background-image: url('../images/responsive.png');
}

.code-quality-icon {
  background-image: url('../images/code-quality.png');
}

@media screen and (max-width: 600px) {
  .quality {
    margin: 24px;
  }

  .reliability-title {
    font-size: 20px;
    width: 150px;
  }
}



